.knowledgeTest-wrap .curriculum-top {
  width: 100%;
  margin-bottom: 20px;
}
.knowledgeTest-wrap .paper-right-menu {
  background: #fff;
  border-radius: 20px;
  border: 4px solid #ffffff;
  padding: 15px;
  min-height: 500px;
}
.knowledgeTest-wrap .paper-right-menu .ant-select {
  width: 100%;
}
.knowledgeTest-wrap .paper-right-menu .ant-select-selection {
  border-radius: 20px;
  border: none;
  background: #f8f8f8;
}
.knowledgeTest-wrap .paper-right-menu .ant-select-arrow .ant-select-arrow-icon svg {
  color: #27CDA2;
}
.knowledgeTest-wrap .paper-right-menu .paper-common .ant-radio-button-wrapper {
  margin: 0 10px 5px 0;
  color: #000;
  background: none;
  border: none;
}
.knowledgeTest-wrap .paper-right-menu .paper-common .ant-radio-button-wrapper::before {
  width: 0;
}
.knowledgeTest-wrap .paper-right-menu .paper-common .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  background: #27CDA2;
  border-radius: 5px;
  height: 24px;
  line-height: 24px;
  color: #fff;
}
.knowledgeTest-wrap .paper-right-menu .paper-common .actives {
  color: #2cbde6;
  border-color: #2cbde6;
}
.knowledgeTest-wrap .ant-btn-link {
  color: #000 !important;
}
.knowledgeTest-wrap .ant-btn span {
  font-size: 13px;
}
.knowledgeTest-wrap .retrieval-wrap {
  margin: 0px 0 15px;
}
.knowledgeTest-wrap .retrieval-wrap .ant-radio-group-solid .ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled) {
  background: #27CDA2;
  color: #fff;
  border-radius: 20px ;
}
.knowledgeTest-wrap .retrieval-wrap .ant-radio-button-wrapper {
  border: none;
  background: none;
  margin-right: 20px;
}
.knowledgeTest-wrap .retrieval-wrap .ant-radio-button-wrapper:not(:first-child)::before {
  position: absolute;
  top: 0;
  left: -1px;
  display: block;
  width: 0px;
  height: 100%;
  content: '';
}
.knowledgeTest-wrap .noopsy-top-content {
  background: #fff;
  border-radius: 20px;
  border: 4px solid #ffffff;
  padding: 15px;
  margin-bottom: 15px;
}
.knowledgeTest-wrap .noopsy-top-content .noopsy-select {
  color: #333;
  font-size: 14px;
  font-weight: bold;
}
.knowledgeTest-wrap .noopsy-top-content .year-group .ant-checkbox-checked .ant-checkbox-inner {
  background-color: #27cda2;
  border-color: #27cda2;
}
.knowledgeTest-wrap .noopsy-top-content .source-set .ant-checkbox {
  display: none;
}
.knowledgeTest-wrap .noopsy-top-content .source-set .ant-checkbox-wrapper {
  display: block;
  float: left;
  margin-right: 10px;
  margin-bottom: 10px;
  width: 110px;
  height: 28px;
  line-height: 28px;
  color: #999;
  background: #e8e8e8;
  font-size: 14px;
  text-align: center;
  border-radius: 5px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-left: 0;
}
.knowledgeTest-wrap .noopsy-top-content .source-set .ant-checkbox-wrapper-checked {
  background: #27cda2;
  color: #fff;
}
.knowledgeTest-wrap .noopsy-top-content .noopsy-empty {
  text-align: right;
  margin-right: 20px;
  font-size: 13px;
  color: #666;
  cursor: pointer;
}
.knowledgeTest-wrap .noopsy-top-content .noopsy-content-empty {
  color: #999;
  font-size: 14px;
}
.knowledgeTest-wrap .noopsy-top-content .noopsy-select-item {
  margin-right: 10px;
  margin-bottom: 10px;
  z-index: 1;
  line-height: 30px;
  border: 1px solid #ddd;
  text-align: center;
  border-radius: 5px;
  background: #fff;
}
.knowledgeTest-wrap .noopsy-top-content .ant-radio-button-wrapper {
  margin-right: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
}
.knowledgeTest-wrap .noopsy-top-content .noopsy-m20 {
  margin-bottom: 20px;
}
.knowledgeTest-wrap .order-pay-mode .ant-checkbox-group-item {
  width: 97px;
  height: 33px;
  line-height: 32px;
  text-align: center;
  border: 1px solid #e2e2e2;
  box-sizing: border-box;
}
.knowledgeTest-wrap .order-pay-mode .ant-checkbox-wrapper-checked {
  border: 2px solid #27cda2 !important;
  width: 97px;
  height: 32px;
  line-height: 30px;
  text-align: center;
  box-sizing: border-box;
  position: relative;
}
.knowledgeTest-wrap .order-pay-mode .ant-checkbox-wrapper-checked:before {
  content: url("../../../../assets/img/tip.png");
  position: absolute;
  right: 0;
  bottom: -11px;
}
.knowledgeTest-wrap .order-pay-mode .ant-radio {
  display: none;
}
.knowledgeTest-wrap .order-pay-mode img {
  display: block;
  width: 102px;
  height: 64px;
  margin-bottom: 10px;
}
.knowledgeTest-wrap .order-pay-mode .ant-checkbox-inner {
  display: none!important;
}
.knowledgeTest-wrap .order-pay-mode .ant-checkbox-wrapper {
  display: block;
  float: left;
  margin-right: 10px;
  margin-bottom: 10px;
  width: 118px;
  height: 28px;
  line-height: 28px;
  color: #999;
  background: #fff;
  font-size: 14px;
  text-align: center;
  border: 1px solid #adadad;
  border-radius: 5px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-left: 0;
}
.knowledgeTest-wrap .order-pay-mode .ant-checkbox {
  display: none;
}
.knowledgeTest-wrap .noopsy-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}
.knowledgeTest-wrap .noopsy-row .ant-input[disabled] {
  width: 50px;
}
.knowledgeTest-wrap .noopsy-row .ant-col-2 {
  width: 110px!important;
}
.knowledgeTest-wrap .noopsy-row .ant-col-sm-6 {
  width: 40%;
}
.knowledgeTest-wrap .noopsy-row .ant-form-item label {
  padding-right: 40px;
}
.knowledgeTest-wrap .noopsy-hard {
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
  font-size: 16px;
  color: #333;
}
.knowledgeTest-wrap .noopsy-hard .noopsy-factor {
  color: red;
}
.knowledgeTest-wrap .noopsy-bottom {
  display: block;
  margin: 20px auto 40px;
  width: 200px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  background: #27cda2;
  font-size: 14px;
  text-align: center;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
}
.knowledgeTest-wrap .m20 {
  margin-bottom: 15px;
}
.knowledgeTest-wrap .ant-select-selection {
  margin-right: 15px;
}
.knowledgeTest-wrap .form-number {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.knowledgeTest-wrap .ant-form-item {
  margin-bottom: 0!important;
}
.knowledgeTest-wrap .generate-test {
  display: block;
  margin: 20px auto 40px;
  width: 200px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  background: #27cda2 !important;
  font-size: 14px;
  text-align: center;
  border-radius: 5px;
  border: 0;
}
.knowledgeTest-wrap .ant-cascader-picker {
  border-radius: 20px;
  border: none;
  background: #f8f8f8;
  width: 100%;
}
.knowledgeTest-wrap .ant-cascader-input.ant-input {
  border: none;
}
.knowledgeTest-wrap .ant-cascader-menu {
  width: 282px!important;
}
.knowledgeTest-wrap .ant-input {
  border-radius: 5px!important;
}
